* {
  box-sizing: border-box;
}

body {
  padding: 0;
  margin: 0;
  font-family: "Montserrat", sans-serif;
  background-color: #fef3d4 !important;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: @black;
}

main {
  min-height: 78vh;
}

ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

ol {
  padding: 0;
  margin: 0;
}

p {
  margin: 0;
}

.vis-hidd {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  /* IE6, IE7 */
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

a {
  text-decoration: none;
  transition: all ease-in 0.3s;
  outline: none;
}

button {
  outline: none;
  cursor: pointer;
  transition: all ease-in 0.2s;

  &:hover {
    opacity: 0.8;
  }

  &:active,
  &:focus {
    opacity: 0.6;
  }
}

.overlay {
  width: 100%;
  height: 100%;
  z-index: 8;
  background-color: rgba(255, 255, 255, 0.4);
  position: fixed;
  top: 0;
  left: 0;
  backdrop-filter: blur(8px);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}
