* {
  box-sizing: border-box;
}
body {
  padding: 0;
  margin: 0;
  font-family: "Montserrat", sans-serif;
  background-color: #fef3d4 !important;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: #49312B;
}
main {
  min-height: 78vh;
}
ul {
  padding: 0;
  margin: 0;
  list-style: none;
}
ol {
  padding: 0;
  margin: 0;
}
p {
  margin: 0;
}
.vis-hidd {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  /* IE6, IE7 */
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}
a {
  text-decoration: none;
  transition: all ease-in 0.3s;
  outline: none;
}
button {
  outline: none;
  cursor: pointer;
  transition: all ease-in 0.2s;
}
button:hover {
  opacity: 0.8;
}
button:active,
button:focus {
  opacity: 0.6;
}
.overlay {
  width: 100%;
  height: 100%;
  z-index: 8;
  background-color: rgba(255, 255, 255, 0.4);
  position: fixed;
  top: 0;
  left: 0;
  backdrop-filter: blur(8px);
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}
.micro-credit__banner {
  padding-top: 60px;
  padding-bottom: 80px;
  background-image: url('../img/mic-zaim-bg.jpg');
  background-position: 50%;
  background-size: cover;
  background-repeat: no-repeat;
}
.micro-credit__banner h1 {
  font-size: 40px;
  font-style: normal;
  font-weight: 700;
  line-height: 49px;
  text-align: left;
  color: #ffffff;
  margin-bottom: 40px;
}
@media screen and (max-width: 576px) {
  .micro-credit__banner h1 {
    text-align: center;
  }
}
.micro-credit__banner-content {
  max-width: 480px;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.7);
  padding: 40px;
  border-radius: 5px;
}
@media screen and (max-width: 576px) {
  .micro-credit__banner-content {
    text-align: center;
  }
}
.micro-credit__banner-content p {
  font-size: 16px;
  font-style: normal;
  font-weight: 300;
  line-height: 29px;
  margin-bottom: 40px;
}
.micro-credit__btn {
  display: inline-block;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 29px;
  letter-spacing: 0.04em;
  color: #ffffff !important;
  padding: 8px 19px;
  text-transform: uppercase;
  border-radius: 5px;
  border: none;
}
.micro-credit__btn.micro-credit__btn--red {
  background-color: #953014;
}
.micro-credit__btn.micro-credit__btn--transparent {
  background-color: transparent;
  color: #49312B !important;
}
.micro-credit__info-wrapper {
  padding-top: 80px;
  padding-bottom: 40px;
  margin-left: auto;
  margin-right: auto;
}
@media screen and (max-width: 576px) {
  .micro-credit__info-wrapper {
    text-align: center;
  }
}
.micro-credit__info-wrapper .micro-credit__info-title {
  font-size: 40px;
  font-style: normal;
  font-weight: 700;
  line-height: 49px;
  margin-bottom: 40px;
  text-align: center;
}
.micro-credit__info-wrapper .micro-credit__info-dscr {
  font-size: 16px;
  font-style: normal;
  font-weight: 300;
  line-height: 29px;
  max-width: 600px;
  width: 100%;
}
@media screen and (max-width: 576px) {
  .micro-credit__info-wrapper .micro-credit__info-dscr {
    padding: 0 20px;
  }
}
.micro-credit__form-wrapper {
  padding-top: 40px;
  padding-bottom: 180px;
  margin-left: auto;
  margin-right: auto;
}
.micro-credit__form-wrapper .micro-credit__form-title {
  font-size: 40px;
  font-style: normal;
  font-weight: 700;
  line-height: 49px;
  margin-bottom: 40px;
  text-align: center;
}
.micro-credit__form {
  width: 510px;
  text-align: center;
}
@media screen and (max-width: 576px) {
  .micro-credit__form {
    width: 100%;
    max-width: 100%;
  }
}
.micro-credit__form .micro-credit__from-wrapper {
  margin-bottom: 25px;
}
.micro-credit__form label {
  font-size: 18px;
  font-style: normal;
  font-weight: 600;
  line-height: 22px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.micro-credit__form label span {
  max-width: 156px;
  text-align: left;
}
.micro-credit__form label span span {
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 17px;
}
@media screen and (max-width: 576px) {
  .micro-credit__form label span {
    margin-bottom: 20px;
    max-width: 280px;
  }
}
@media screen and (max-width: 576px) {
  .micro-credit__form label {
    flex-direction: column;
  }
}
.micro-credit__form .micro-credit__form-input {
  max-width: 310px;
  width: 100%;
  background-color: #ffffff;
  border: none;
  border-radius: 5px;
  font-size: 18px;
  font-style: normal;
  font-weight: 600;
  line-height: 22px;
  padding-top: 6px;
  padding-bottom: 7px;
}
.micro-credit__form .micro-credit__btn {
  margin-top: 60px;
}
.micro-credit__form-block {
  padding-top: 100px;
  padding-bottom: 180px;
}
.micro-credit__mid-title {
  font-size: 24px;
  font-style: normal;
  font-weight: 600;
  line-height: 29px;
  margin-bottom: 18px;
}
.decor {
  width: 100%;
  height: 2px;
  background-color: #953014;
}
.micro-credit__form-steps {
  width: 322px;
  display: flex;
  justify-content: space-between;
  position: relative;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 40px;
}
.micro-credit__form-steps li {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.micro-credit__form-steps li span {
  width: 102px;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 20px;
  text-align: center;
}
.micro-credit__form-steps::before {
  content: "";
  width: 47px;
  height: 2px;
  background-image: url("../img/line-dash.png");
  background-size: 47px 2px;
  background-position: 0 0;
  background-repeat: no-repeat;
  display: block;
  position: absolute;
  top: 26px;
  left: 82px;
  transform: translateY(-50%);
}
.micro-credit__form-steps::after {
  content: "";
  width: 47px;
  height: 2px;
  background-image: url("../img/line-dash.png");
  background-size: 47px 2px;
  background-position: 0 0;
  background-repeat: no-repeat;
  display: block;
  position: absolute;
  top: 26px;
  right: 82px;
  transform: translateY(-50%);
}
.micro-credit__form-steps .micro-credit__step {
  width: 52px;
  height: 52px;
  border-radius: 50%;
  background-color: #ffffff;
  font-size: 24px;
  font-style: normal;
  font-weight: 600;
  line-height: 29px;
  text-align: center;
  padding-top: 12px;
  margin-bottom: 10px;
}
.micro-credit__form-steps .micro-credit__step.micro-credit__step--active {
  background-color: #953014;
  color: #ffffff;
}
.micro-credit__from-btns-wrapper {
  display: flex;
  justify-content: flex-end;
  padding: 0 12px;
}
@media screen and (max-width: 576px) {
  .micro-credit__from-btns-wrapper {
    justify-content: space-around;
  }
}
.micro-credit__from-btns-wrapper .micro-credit__btn--red {
  margin-left: 40px;
}
.micro-credit__form-wrapper--main {
  margin-left: 0;
  margin-right: 0;
  width: 100%;
}
.micro-credit__form-wrapper--main .micro-credit__form--main {
  width: 100%;
  max-width: 880px;
  margin-left: auto;
  margin-right: auto;
}
.micro-credit__form-wrapper--main .micro-credit__form--main .micro-credit__from-wrapper {
  width: 100%;
  max-width: 510px;
  margin-left: auto;
  margin-right: auto;
}
.micro-credit__form-wrapper--main .micro-credit__form-input--file {
  opacity: 0;
  visibility: hidden;
  position: absolute;
}
.micro-credit__form-wrapper--main .micro-credit__from-wrapper--file label {
  position: relative;
}
@media screen and (max-width: 576px) {
}
.micro-credit__form-wrapper--main .micro-credit__from-wrapper--file label::after {
  content: "+";
  display: block;
  width: 44px;
  height: 44px;
  background: #ffffff;
  padding-top: 12px;
  border-radius: 5px;
  margin-top: -5px;
  margin-right: 266px;
  cursor: pointer;
}
@media screen and (max-width: 576px) {
  .micro-credit__form-wrapper--main .micro-credit__from-wrapper--file label::after {
    margin-right: 0;
  }
}
.micro-credit__from-wrapper--agree {
  display: flex;
  justify-content: flex-end;
}
@media screen and (max-width: 576px) {
  .micro-credit__from-wrapper--agree {
    justify-content: center;
  }
}
.micro-credit__from-wrapper--agree .micro-credit__form-input--agree {
  width: auto;
  margin-right: 10px;
  margin-top: 4px;
}
.micro-credit__from-wrapper--agree label {
  width: 310px;
  align-items: flex-start;
}
@media screen and (max-width: 576px) {
  .micro-credit__from-wrapper--agree label {
    flex-direction: row;
    width: 280px;
  }
}
.micro-credit__from-wrapper--agree label span {
  max-width: 100%;
}
.micro-credit__from-wrapper--agree label span span {
  font-size: 12px;
}
.credit-table {
  font-size: 16px;
  background-color: #ffffff;
  padding-left: 32px;
  padding-right: 32px;
  padding-bottom: 27px;
  margin-top: 20px;
  margin-left: auto;
  margin-right: auto;
  overflow-x: scroll;
  scrollbar-color: #49312B #ffffff;
  /* «цвет ползунка» «цвет полосы скроллбара» */
  scrollbar-width: thin;
  /* полоса прокрутки (скроллбар) */
  /* ползунок скроллбара */
}
.credit-table::-webkit-scrollbar {
  width: 8px;
  /* ширина для вертикального скролла */
  height: 8px;
  /* высота для горизонтального скролла */
  background-color: #ffffff;
}
.credit-table::-webkit-scrollbar-thumb {
  background-color: #49312B;
  border-radius: 9em;
}
.credit-table .credit-table__row {
  display: flex;
  align-items: center;
  width: 1140px;
  padding-top: 12px;
  padding-bottom: 12px;
  min-height: 70px;
  border-bottom: 1px solid #DEE2E6;
}
.credit-table .credit-table__row div {
  width: 140px;
  margin-right: 10px;
}
.credit-table .credit-table__row div span {
  font-size: 12px;
}
.credit-table .credit-table__row div:last-child {
  margin-right: 0;
  flex-grow: 1;
  text-align: center;
}
.credit-table .sucess span {
  color: #11D000;
}
.credit-table .failure span {
  color: #FF0000;
}
.credit-table .waiting span {
  color: inherit;
}
.overlay {
  width: 100%;
  height: 100%;
  position: fixed;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 0;
}
