  }
}
@media screen and (max-width: 768px) {
  .pop-up-add {
    padding-top: 32px;
    padding-bottom: 32px;
    padding-left: 30px;
    padding-right: 30px;
  }
}
@media screen and (max-width: 576px) {
  .pop-up-add {
    padding-top: 22px;
    padding-bottom: 22px;
    padding-left: 20px;
    padding-right: 20px;
  }
}
.pop-up-add .pop-up-title {
  font-size: 40px;
  font-style: normal;
  font-weight: 700;
  line-height: 49px;
}
@media screen and (max-width: 576px) {
  .pop-up-add .pop-up-title {
    font-size: 26px;
    line-height: 29px;
    font-weight: 500;
    margin-bottom: 20px;
  }
}
.pop-up-add .pop-up-sum {
  font-size: 80px;
  font-style: normal;
