.pop-up-add .pop-up-add__lost-link {
  font-size: 12px;
  font-style: normal;
  font-weight: 600;
  line-height: 15px;
  text-decoration: underline;
  color: #49312B;
  display: inline-block;
}
.pop-up-add.pop-up-add--lost-pass-step-1,
.pop-up-add.pop-up-add--lost-pass-step-2 {
  min-height: 488px;
}
.pop-up-add.pop-up-add--lost-pass-step-1 .pop-up-title,
.pop-up-add.pop-up-add--lost-pass-step-2 .pop-up-title {
  margin-bottom: 20px;
}
.pop-up-add.pop-up-add--lost-pass-step-1 .pop-up-txt,
.pop-up-add.pop-up-add--lost-pass-step-2 .pop-up-txt {
  margin-bottom: 40px;
}
.micro-credit__form--pop-up {
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 20px;
}
.micro-credit__form--pop-up .micro-credit__form-input {
  background-color: #fef3d4;
  border: 1px solid transparent;
}
.micro-credit__form--pop-up .micro-credit__btn {
  margin-top: 40px;
}
@media screen and (max-width: 576px) {
  .micro-credit__form--pop-up .micro-credit__btn {
    margin-top: 20px;
  }
}
.micro-credit__form-input--error {
  border: 1px solid #953014 !important;
}
