.micro-credit__banner {
  padding-top: 60px;
  padding-bottom: 80px;
  background-image: url('../img/mic-zaim-bg.jpg');
  background-position: 50%;
  background-size: cover;
  background-repeat: no-repeat;

  h1 {
    font-size: 40px;
    font-style: normal;
    font-weight: 700;
    line-height: 49px;
    text-align: left;
    color: @white;
    margin-bottom: 40px;

    @media screen and(max-width: 576px) {
      text-align: center;
    }
  }
}

.micro-credit__banner-content {
  max-width: 480px;
  width: 100%;
  background-color: rgba(255, 255, 255, .7);
  padding: 40px;
  border-radius: 5px;

  @media screen and(max-width: 576px) {
    text-align: center;
  }

  p {
    font-size: 16px;
    font-style: normal;
    font-weight: 300;
    line-height: 29px;
    margin-bottom: 40px;
  }
}

.micro-credit__btn {
  display: inline-block;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 29px;
  letter-spacing: 0.04em;
  color: @white  !important;
  padding: 8px 19px;
  text-transform: uppercase;
  border-radius: 5px;
  border: none;

  &.micro-credit__btn--red {
    background-color: @red;
  }

  &.micro-credit__btn--transparent {
    background-color: transparent;
    color: @black  !important;
  }
}

.micro-credit__info-wrapper {
  padding-top: 80px;
  padding-bottom: 40px;
  margin-left: auto;
  margin-right: auto;

  @media screen and(max-width: 576px) {
    text-align: center;
  }

  .micro-credit__info-title {
    font-size: 40px;
    font-style: normal;
    font-weight: 700;
    line-height: 49px;
    margin-bottom: 40px;
    text-align: center;
  }

  .micro-credit__info-dscr {
    font-size: 16px;
    font-style: normal;
    font-weight: 300;
    line-height: 29px;
    max-width: 600px;
    width: 100%;


    @media screen and(max-width: 576px) {
      padding: 0 20px;
    }
  }
}

.micro-credit__form-wrapper {
  padding-top: 40px;
  padding-bottom: 180px;
  margin-left: auto;
  margin-right: auto;

  .micro-credit__form-title {
    font-size: 40px;
    font-style: normal;
    font-weight: 700;
    line-height: 49px;
    margin-bottom: 40px;
    text-align: center;
  }
}

.micro-credit__form {
  width: 510px;
  text-align: center;

  @media screen and(max-width: 576px) {
    width: 100%;
    max-width: 100%;
  }

  .micro-credit__from-wrapper {
    margin-bottom: 25px;
  }

  label {
    font-size: 18px;
    font-style: normal;
    font-weight: 600;
    line-height: 22px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    span {
      max-width: 156px;
      text-align: left;

      span {
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: 17px;
      }

      @media screen and(max-width: 576px) {
        margin-bottom: 20px;
        max-width: 280px;
      }
    }

    @media screen and(max-width: 576px) {
      flex-direction: column;
    }
  }

  .micro-credit__form-input {
    max-width: 310px;
    width: 100%;
    background-color: @white;
    border: none;
    border-radius: 5px;
    font-size: 18px;
    font-style: normal;
    font-weight: 600;
    line-height: 22px;
    padding-top: 6px;
    padding-bottom: 7px;
  }

  .micro-credit__btn {
    margin-top: 60px;
  }
}

.micro-credit__form-block {
  padding-top: 100px;
  padding-bottom: 180px;
}

.micro-credit__mid-title {
  font-size: 24px;
  font-style: normal;
  font-weight: 600;
  line-height: 29px;
  margin-bottom: 18px;
}

.decor {
  width: 100%;
  height: 2px;
  background-color: @red;
}

.micro-credit__form-steps {
  width: 322px;
  display: flex;
  justify-content: space-between;
  position: relative;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 40px;

  li {
    display: flex;
    flex-direction: column;
    align-items: center;

    span {
      width: 102px;
      font-size: 16px;
      font-style: normal;
      font-weight: 400;
      line-height: 20px;
      text-align: center;
    }
  }

  &::before {
    content: "";
    width: 47px;
    height: 2px;
    background-image: url("../img/line-dash.png");
    background-size: 47px 2px;
    background-position: 0 0;
    background-repeat: no-repeat;
    display: block;
    position: absolute;
    top: 26px;
    left: 82px;
    transform: translateY(-50%);
  }

  &::after {
    content: "";
    width: 47px;
    height: 2px;
    background-image: url("../img/line-dash.png");
    background-size: 47px 2px;
    background-position: 0 0;
    background-repeat: no-repeat;
    display: block;
    position: absolute;
    top: 26px;
    right: 82px;
    transform: translateY(-50%);
  }

  .micro-credit__step {
    width: 52px;
    height: 52px;
    border-radius: 50%;
    background-color: @white;
    font-size: 24px;
    font-style: normal;
    font-weight: 600;
    line-height: 29px;
    text-align: center;
    padding-top: 12px;
    margin-bottom: 10px;

    &.micro-credit__step--active {
      background-color: @red;
      color: @white;
    }
  }
}

.micro-credit__from-btns-wrapper {
  display: flex;
  justify-content: flex-end;
  padding: 0 12px;

  @media screen and(max-width: 576px) {
    justify-content: space-around;
  }

  .micro-credit__btn--red {
    margin-left: 40px;
  }
}

.micro-credit__form-wrapper--main {
  margin-left: 0;
  margin-right: 0;
  width: 100%;

  .micro-credit__form--main {
    width: 100%;
    max-width: 880px;
    margin-left: auto;
    margin-right: auto;

    .micro-credit__from-wrapper {
      width: 100%;
      max-width: 510px;
      margin-left: auto;
      margin-right: auto;
    }
  }

  .micro-credit__form-input--file {
    opacity: 0;
    visibility: hidden;
    position: absolute;
  }

  .micro-credit__from-wrapper--file label {
    position: relative;

    @media screen and(max-width: 576px) {
      // display: block;
    }

    &::after {
      content: "+";
      display: block;
      width: 44px;
      height: 44px;
      background: @white;
      padding-top: 12px;
      border-radius: 5px;
      margin-top: -5px;
      margin-right: 266px;
      cursor: pointer;

      @media screen and(max-width: 576px) {
        margin-right: 0;
      }
    }
  }
}

.micro-credit__from-wrapper--agree {
  display: flex;
  justify-content: flex-end;

  @media screen and(max-width: 576px) {
    justify-content: center;
  }

  .micro-credit__form-input--agree {
    width: auto;
    margin-right: 10px;
    margin-top: 4px;
  }

  label {
    width: 310px;
    align-items: flex-start;

    @media screen and(max-width: 576px) {
      flex-direction: row;
      width: 280px;
    }

    span {
      max-width: 100%;

      span {
        font-size: 12px;
      }
    }
  }
}

.credit-table {
  font-size: 16px;
  background-color: @white;
  padding-left: 32px;
  padding-right: 32px;
  padding-bottom: 27px;
  margin-top: 20px;
  margin-left: auto;
  margin-right: auto;
  overflow-x: scroll;
  // скролл для firefox
  scrollbar-color: @black @white;
  /* «цвет ползунка» «цвет полосы скроллбара» */
  scrollbar-width: thin;

  // скролл для webkit
  /* полоса прокрутки (скроллбар) */
  &::-webkit-scrollbar {
    width: 8px;
    /* ширина для вертикального скролла */
    height: 8px;
    /* высота для горизонтального скролла */
    background-color: @white;
  }

  /* ползунок скроллбара */
  &::-webkit-scrollbar-thumb {
    background-color: @black;
    border-radius: 9em;
  }

  .credit-table__row {
    display: flex;
    align-items: center;
    width: 1140px;
    padding-top: 12px;
    padding-bottom: 12px;
    min-height: 70px;
    border-bottom: 1px solid #DEE2E6;

    div {
      width: 140px;
      margin-right: 10px;

      span {
        font-size: 12px;
      }

      &:last-child {
        margin-right: 0;
        flex-grow: 1;
        text-align: center;
      }
    }
  }

  .sucess {
    span {
      color: #11D000;
    }
  }

  .failure {
    span {
      color: #FF0000;
    }
  }

  .waiting {
    span {
      color: inherit;
    }
  }
}


.overlay {
  width: 100%;
  height: 100%;
  position: fixed;
  background-color: rgba(0, 0, 0, .5);
  z-index: 0;
}

.pop-up-add {
  background-color: @white;
  width: 60%;
  min-height: 688px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding-top: 92px;
  padding-bottom: 82px;
  padding-left: 100px;
  padding-right: 100px;
  text-align: center;

  @media screen and(max-width: 1366px) {
    padding-top: 52px;
    padding-bottom: 42px;
    padding-left: 50px;
    padding-right: 50px;
    min-height: 600px;
  }

  @media screen and(max-width: 768px) {
    padding-top: 52px;
    padding-bottom: 42px;
    padding-left: 50px;
    padding-right: 50px;
    width: 90%;
  }

  @media screen and(max-width: 768px) {
    padding-top: 32px;
    padding-bottom: 32px;
    padding-left: 30px;
    padding-right: 30px;
  }

  @media screen and(max-width: 576px) {
    padding-top: 22px;
    padding-bottom: 22px;
    padding-left: 20px;
    padding-right: 20px;
  }

  .pop-up-title {
    font-size: 40px;
    font-style: normal;
    font-weight: 700;
    line-height: 49px;

    @media screen and(max-width: 576px) {
      font-size: 26px;
      line-height: 29px;
      font-weight: 500;
      margin-bottom: 20px;
    }
  }

  .pop-up-sum {
    font-size: 80px;
    font-style: normal;
    font-weight: 600;
    line-height: 98px;
    display: block;
    margin-bottom: 60px;

    @media screen and(max-width: 576px) {
      font-size: 32px;
      line-height: 32px;
      margin-bottom: 20px;
    }
  }

  .pop-up-txt {
    font-size: 16px;
    font-style: normal;
    font-weight: 600;
    line-height: 20px;
    max-width: 420px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 24px;

    @media screen and(max-width: 576px) {
      font-weight: 500;
    }
  }

  .pop-up-add__lost-link {
    font-size: 12px;
    font-style: normal;
    font-weight: 600;
    line-height: 15px;
    text-decoration: underline;
    color: @black;
    display: inline-block;
  }

  &.pop-up-add--lost-pass-step-1,
  &.pop-up-add--lost-pass-step-2 {
    min-height: 488px;

    .pop-up-title {
      margin-bottom: 20px;
    }

    .pop-up-txt {
      margin-bottom: 40px;
    }
  }
}

.micro-credit__form--pop-up {
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 20px;

  .micro-credit__form-input {
    background-color: #fef3d4;
    border: 1px solid transparent;
  }

  .micro-credit__btn {
    margin-top: 40px;

    @media screen and(max-width: 576px) {
      margin-top: 20px;
    }
  }
}

.micro-credit__form-input--error {
  border: 1px solid @red  !important;
}
